<template>
    <div>
        <button class="btn" @click="openModal">Modifier</button>
        <GenericModal :showModal="showModal" @close="closeModal">
            <template #header>
                <h2>Modification de la catgorie</h2>
            </template>
            <!-- Le contenu principal utilise le slot par dfaut -->
            <form id="editCategory" enctype='multipart/form-data' @submit.prevent="$event => editCategory()">
                <div class="banner-content">
                    <label for="imgCategoryEdit" class="label-changeFile">
                        <svg class="icon upload-icon">
                            <use href="@/assets/icons/icons.svg#upload"></use>
                        </svg>
                        <input class="changeFile" id="imgCategoryEdit" type="file" @change="$event => selectFile($event)"
                            accept="image/png, image/jpg, image/jpeg" />
                    </label>

                    <div class="picture">
                        <img v-if="this.Category.imgCategory" class="oldImg" :class="{ 'newImg': this.Category.newFile }"
                            :src="this.Category.imgCategory">
                    </div>
                </div>

                <ul class="form-style-1">

                    <p v-if="errors.length">
                        <b>Corriger les erreurs suivantes</b>
                    <ul>
                        <li v-for="error in errors">{{ error }}</li>
                    </ul>
                    </p>

                    <li>
                        <label for="nom">Nom de la Catgorie</label>
                        <input type="text" name="nom" class="field-divided" placeholder="Nom" v-model="Category.titleCategory"
                            required />
                    </li>

                    <li>
                        <label for="Categorydescription'">Description</label>
                        <input type="text" name="Categorydescription" class="field-long" placeholder="description"
                            v-model="Category.descriptionCategory" required />
                    </li>
                    
                </ul>
            </form>

            <template #footer>
                <button type="submit" form="editCategory" class="btn">Modifier</button>
                <button type="button" class="btn delete" @click="$event => goDeleteCategory()">SUPPRIMER</button>
            </template>

        </GenericModal>

    </div>
</template>
  
<script>
import GenericModal from '@/components/GenericModal.vue';
import { productService } from '@/services/product.service';

export default {
    components: {
        GenericModal
    },

    props:{
    categoryEdit: {
        type: Object,
        required: false,
        default: () => ({
            titleCategory: '',
                descriptionCategory: '',
                imgCategory: '',
        })
    }
},
    data() {
        return {
            showModal: false,
            Category: {
                titleCategory: '',
                descriptionCategory: '',
                imgCategory: '',
                imageFile: null
            },

            allProducts: [],
            selectedProducts: [],
            showProductList: false,


            errors: []
        };
    },
    watch: {
    categoryEdit: {
        immediate: true, // pour excuter le handler ds l'initialisation
        deep: true,     // pour surveiller les proprits imbriques de l'objet
        handler(newValue) {
            this.Category = { ...newValue };
        }
    }
},

    methods: {

        openModal() {
        this.showModal = true;

      },

      closeModal() {
        this.showModal = false;
      },

      selectFile(event) {
            const file = event.target.files[0];
            this.Category.imageFile = event.target.files[0];
            if (file) {
                this.Category.imgCategory = URL.createObjectURL(file);
                this.Category.newFile = file;
            }
        },

        editCategory() {
            const formData = new FormData();
            formData.append('titleCategory', this.Category.titleCategory);
            formData.append('descriptionCategory', this.Category.descriptionCategory);
            formData.append('imgCategory', this.Category.imageFile);

            productService.editCategory(this.Category._id, formData)
                .then(res => {
                    if (
                        res.data.modif.acknowledged == true
                        && res.data.modif.matchedCount == 1
                        && res.data.modif.modifiedCount == 0
                    ) {
                        console.log('aucune modif');
                    } else {
                        this.Category.newFile = null
                        console.log(res.data)
                        this.closeModal();


                      // mettre  jour le produit dans l'index
                      this.$emit('category-updated', this.Category);
                    }
                })
                .catch(err => console.log(err))
        },

        goDeleteCategory() {
            this.closeModal();
            this.$emit('category-deleted',this.Category)
            // productService.deleteCategory(this.Category._id)
            //     .then(res => {
            //         console.log(res.data);
            //         this.closeModal();
            //         this.$emit('category-deleted',this.Category)
            //     })
            //     .catch(err => console.log(err))
        }


    },

}

</script>

<style scoped>

.banner-content {
    overflow: hidden;
    border-radius: 12px;
    position: relative;
    padding-top: 53.33%;
    border: 5px solid var(--textColor);
    margin: 18px 0px;
}

.banner-content img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;
}

.upload-icon {
    background-color: white;
    border-radius: 12px;
    width: 40px;
    height: 33px;
    padding: 5px;
    box-shadow: 0px 0px 5px 3px #00000030;
}

.label-changeFile {
    cursor: pointer;
    z-index: 1;
    position: absolute;
    translate: -50%, -50%;
    transform: translate(50%, -50%);
    top: 50%;
    right: 50%;
}

.changeFile {
    display: none;

}

.newImg {
    filter: brightness(0.7);
}

</style>
  