<template>
    <div>
        <button class="btn" @click="openModal">Crer une Catgorie</button>
        <GenericModal :showModal="showModal" @close="closeModal">
            <template #header>
                <h2>Crer une catgorie</h2>
            </template>

            <!-- Le contenu principal utilise le slot par dfaut -->
            <form id="createCategory" enctype='multipart/form-data' @submit.prevent="submitCategory">
                <ul class="form-style-1">
                    <p v-if="errors.length">
                        <b>Corriger les erreurs suivantes</b>
                    <ul>
                        <li v-for="error in errors">{{ error }}</li>
                    </ul>
                    </p>

                    <li>
                        <label for="categoryName">Nom de la Catgorie</label>
                        <input type="text" id="categoryName" class="field-divided" placeholder="Nom"
                            v-model="Category.titleCategory" required />
                    </li>

                    <li>
                        <label for="Categorydescription">description</label>
                        <input type="text" name="Categorydescription" class="field-long" placeholder="description"
                            v-model="Category.descriptionCategory" />
                    </li>

                    <!-- <button @click="showProductList = !showProductList">Ajouter des produits</button>
                    <ul v-if="showProductList">
                        <li v-for="product in allProducts" :key="product._id">
                            <input type="checkbox" v-model="selectedProducts" :value="product._id">{{ product.nameProduct }}
                        </li>
                    </ul> -->

                    <li>
                        <label for="categoryImage">Image</label>
                        <input name="categoryImage" type="file" class="btn fileSelector" @change="selectFile" required>
                    </li>

                </ul>
            </form>
            <template #footer>
                <button type="submit" form="createCategory" class="btn">Crer</button>
                <button @click="closeModal" class="btn delete">Annuler</button>
            </template>
        </GenericModal>

    </div>
</template>
  
<script>
import GenericModal from '@/components/GenericModal.vue';
import { productService } from '@/services/product.service';

export default {
    components: {
        GenericModal
    },

    // props: {
    //     ensembleId: {
    //         type: String, 
    //         required: false
    //     }
    // },


    data() {
        return {
            showModal: false,
            Category: {
                titleCategory: '',
                descriptionCategory: '',
                imgCategory: '',
            },

            allProducts: [],
            selectedProducts: [],
            showProductList: false,


            errors: []
        };
    },
    methods: {
        openModal() {
            this.showModal = true;
        },
        closeModal() {
            this.showModal = false;
        },

        selectFile(event) {
            this.Category.imgCategory = event.target.files[0]
        },


        submitCategory() {
            // const id = this.ensembleId
            const formData = new FormData();
            formData.append('titleCategory', this.Category.titleCategory);
            formData.append('descriptionCategory', this.Category.descriptionCategory);
            formData.append('imgCategory', this.Category.imgCategory);
            // formData.append('products', JSON.stringify(this.selectedProducts));
            productService.createCategory(formData)
                .then(res => {
                    console.log(res)
                    this.$emit('category-added', res.data.createdCategory);
                    this.showModal = false

                })
                .catch(err => console.log(err))

        },

    },

};
</script>
  