<template>
    <div>
        <CategoryCreationModal @category-added="addNewCategory" />
        <p>{{ message }}</p>
    </div>

    <div>
        <ProductEditModal ref="editModal" :productEdit="productToEdit" @product-updated="updateProduct"
            @product-deleted="handleProductDeleted" />

        <draggable :list="categories" handle=".grabWrapper" ghost-class="ghost" drag-class="drag" item-key="_id"
            @end="updateCategoryOrder">
            <template #item="{ element: category, index }">
                <div :key="category._id" class="category module">
                    <div class="headerCategory">
                        <div class="grabWrapper">
                            <i class="grab" aria-hidden="true">drag_indicator</i>
                        </div>
                        <h1 class="titleCategory" @click="toggleProducts(category._id)">{{ category.titleCategory }}</h1>
                        <CategoryEditModal ref="editCategoryModal" :categoryEdit="category"
                        @category-updated="updateCategory" @category-deleted="deleteCategory" />
                    </div>
                    <h2 v-show="!showProducts[category._id]">cliquez pour voir {{ category.products.length }} produits</h2>
                    
                    <div class="categoryContent" v-if="showProducts[category._id]">
                        
                        <div class="actionDiv">
                            <ProductCreationModal @submit-product="handleProductSubmit" :categoryId="category._id" />
                        </div>

                        <draggable tag="ul" :list="category.products" class="rayon" handle=".grabWrapper" item-key="_id"
                            ghost-class="ghost" drag-class="drag" @end="updateProductOrder(category)">
                            <template #item="{ element, index }">
                                <li class="produit" :key="element._id">
                                    <div class="grabWrapper">
                                        <i class="grab" aria-hidden="true">drag_indicator</i>
                                    </div>
                                    <div class="pictureProduit">
                                        <img :src="element.imageUrl" class="imgproduit">
                                    </div>
                                    <div class="info" @click="openEditModal(element)">
                                        <div class="titleandinfo">
                                            <h2 class="titleproduit">{{ element.nameProduct }}</h2>
                                            <h3>{{ element.descriptionProduct }}</h3>
                                        </div>
                                    </div>
                                    <div class="prixProduit" @click.stop="">
                                        <input type="number" name="prixMenu" step="0.10" class="field-long"
                                            placeholder="prix" pattern="^\d+(\.\d{1,2})?$" max="99.99"
                                            v-model="element.prixProduct"
                                            @change="expressPricing(element._id, element.prixProduct)">
                                    </div>
                                </li>
                            </template>
                        </draggable>
                    </div>
                </div>
            </template>
        </draggable>
    </div>
</template>

  
<script>
import { productService } from '@/services/product.service'
import ProductCreationModal from '@/components/ProductCreationModal.vue';
import ProductEditModal from '@/components/ProductEditModal.vue';
import CategoryCreationModal from '@/components/CategoryCreationModal.vue';
import CategoryEditModal from '@/components/CategoryEditModal.vue';


import draggable from 'vuedraggable';

export default {
    name: "categoryIndex",
    components: {
        draggable,
        ProductCreationModal,
        ProductEditModal,
        CategoryCreationModal,
        CategoryEditModal
    },
    data() {
        return {
            drag: false,
            categories: [],
            categoryName: '',
            productsList: [],
            selectedProduct: null,
            addedProducts: [],
            message: '',

            productToEdit: {},
            categoryToEdit: {},

            dragging: false,

            showCategory: true,
            showProducts: {}

        };
    },


    created() {
        this.getAllCategories()
    },
    methods: {

        getAllCategories() {
            productService.getAllCategories()
                .then(res => {
                    this.categories = res.data

                    // set de showProducts  true pour chaque catgorie
                    this.categories.forEach(category => {
                        this.showProducts[category._id] = false;
                    });

                })
                .catch(err => { console.log(err) })

        },

        toggleProducts(categoryId) {
        this.showProducts[categoryId] = !this.showProducts[categoryId];
    },

        updateCategoryOrder() {
            this.categories.forEach((category, index) => {
                category.orderCategory = index;
            });
            // Prparation des donnes  envoyer
            const orderedCategoryIds = this.categories.map(category => category._id);

            // Appel du service pour mettre  jour l'ordre ct serveur
            productService.updateCategoryOrder(orderedCategoryIds)
                .then(() => {
                    this.message = 'Catgorie mis  jour avec succs !';
                })
                .catch(error => {
                    this.message = 'Erreur lors de la mise  jour de la catgorie';
                    console.error("Erreur:", error);
                });
        },


        updateProductOrder(category) {
            const formCategory = {
                titleCategory: category.titleCategory,
                productsOrder: category.products.map(product => product._id)
            }

            productService.updateProductOrder(category._id, formCategory)
                .then(() => {
                    this.message = 'Catgorie mis  jour avec succs !';
                })
                .catch(error => {
                    this.message = 'Erreur lors de la mise  jour de la catgorie';
                    console.error("Erreur:", error);
                });
        },

        deleteCategory(deletedCategory) {
            productService.deleteCategory(deletedCategory._id)
                .then(res => {
                    console.log(res.data.message);
                    this.categories = this.categories.filter(category => category._id !== deletedCategory._id);
                })
                .catch(err => console.log(err))
        },

        expressPricing(id, newPrice) {
            if (id && newPrice) {
                const formData = new FormData();
                formData.append('prixProduct', newPrice);
                // formData.append('available', this.product.available);
                productService.updateProduct(id, formData)
                    .then(res => {
                        if (
                            res.data.modif.acknowledged == true
                            && res.data.modif.matchedCount == 1
                            && res.data.modif.modifiedCount == 0
                        ) {
                            console.log('aucune modif');
                        } else {
                            // then avec modification effectu
                        }
                    })
                    .catch(err => console.log(err))
            }
        },



        // toggleMenu() {
        //     this.showMenu = !this.showMenu;
        // },
        //     formattedPrice(price) {
        //     return price.toLocaleString('fr-FR', { style: 'currency', currency: 'EUR' });
        // },

        // goEditProduct(id) {
        //     this.$router.push({ name: 'productEdit', params: { id: id } })
        // },

        addProduct() {
            const product = this.productsList.find(p => p._id === this.selectedProduct);
            if (product && !this.addedProducts.includes(product.nameProduct)) {
                this.addedProducts.push(product._id);
            }
        },

        addNewCategory(newCategory) {
            this.categories.push(newCategory);
            this.updateCategoryOrder()
        },
        submitCategory() {
            const categoryData = {
                titleCategory: this.categoryName,
                products: this.addedProducts
            };
            productService.createCategory(categoryData)
                .then((res) => {
                    this.message = 'Catgorie cre avec succs !';
                    this.categoryName = '';
                    this.addedProducts = [];
                    this.categories.push(res.data.createdCategory);
                    updateCategoryOrder()
                })
                .catch(error => {
                    this.message = 'Erreur lors de la cration de la catgorie.';
                    console.error("Erreur:", error);
                });
        },

        openEditModal(product) {
            this.productToEdit = product
            this.$refs.editModal.openModal();
        },

        handleProductSubmit(data) {

            const category = this.categories.find(cat => cat._id === data.categoryId);

            if (category) {
                category.products.push(data.product);
            } else {
                // Grez le cas o la catgorie n'existe pas (ce qui ne devrait normalement pas se produire)
            }


        },

        updateCategory(updatedCategory) {
            const index = this.categories.findIndex(cat => cat._id === updatedCategory._id);
            if (index !== -1) {
                this.categories[index] = updatedCategory;
            }
        },

        updateProduct(updatedProduct) {
            const index = this.categories.findIndex(category =>
                category.products.some(product => product._id === updatedProduct._id));

            if (index !== -1) {
                const productIndex = this.categories[index].products.findIndex(product => product._id === updatedProduct._id);
                if (productIndex !== -1) {
                    // Remplacez le produit par le produit mis  jour
                    this.categories[index].products.splice(productIndex, 1, updatedProduct);
                    this.productToEdit = {};
                }
            }
        },

        handleProductDeleted(deletedProduct) {
            const categoryIndex = this.categories.findIndex(category =>
                category.products.some(product => product._id === deletedProduct._id));

            if (categoryIndex !== -1) {
                const productIndex = this.categories[categoryIndex].products.findIndex(product => product._id === deletedProduct._id);
                if (productIndex !== -1) {
                    // Supprimer le produit de la liste
                    this.categories[categoryIndex].products.splice(productIndex, 1);
                }
            }


        }
    }
}
</script>
  
